import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  Button,
  VStack,
  FormControl,
  FormLabel,
  Input,
  InputGroup,
  InputRightElement,
  Select,
  useToast,
  Text,
} from "@chakra-ui/react";
import { useState } from "react";
import { ChatState } from "../../context/chatProvider";

export function UserDetailsModal({ children }) {
  const {
    user,
    setSelectedChat,
    chatList,
    setChatList,
    openProfile,
    setOpenProfile,
  } = ChatState();
  const toast = useToast();
  const [show, setShow] = useState(false);
  const [newUser, setNewUser] = useState({});
  const [loading, setLoading] = useState(false);
  const [newProfilePic, setNewProfilePic] = useState(false);
  const { isOpen, onOpen, onClose } = useDisclosure();
  return (
    <>
      <span onClick={onOpen}>{children}</span>

      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>edit profile details</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <VStack spacing="5px" color={"b"}>
              <FormControl id="username" isRequired>
                <FormLabel>username</FormLabel>
                <Input
                  value={user ? user.username : ""}
                  placeholder="enter your username"
                  //   onChange={(e) => setNewUser({ ...newUser, username: e.target.value })}
                />
              </FormControl>
              <FormControl id="email" isRequired>
                <FormLabel>email</FormLabel>
                <Input
                  placeholder="enter your email"
                  value={user ? user.email : ""}
                  //   onChange={(e) => setNewUser({ ...newUser, email: e.target.value })}
                />
              </FormControl>
              <FormControl id="password" isRequired>
                <FormLabel>password</FormLabel>
                <InputGroup>
                  <Input
                    type={show ? "text" : "password"}
                    placeholder="enter your password"
                    onChange={(e) =>
                      setNewUser({ ...newUser, password: e.target.value })
                    }
                  />
                  <InputRightElement width={"4.5rem"}>
                    <Button
                      h="1.75rem"
                      size={"sm"}
                      onClick={() => setShow(!show)}
                    >
                      {show ? "Hide" : "Show"}
                    </Button>
                  </InputRightElement>
                </InputGroup>
              </FormControl>
              <FormControl id="confirmPassword" isRequired>
                <FormLabel>confirm password</FormLabel>
                <InputGroup>
                  <Input
                    type={show ? "text" : "password"}
                    placeholder="Retype Password"
                    onChange={(e) =>
                      setNewUser({
                        ...newUser,
                        confirmPassword: e.target.value,
                      })
                    }
                  />
                  <InputRightElement width={"4.5rem"}>
                    <Button
                      h="1.75rem"
                      size={"sm"}
                      onClick={() => setShow(!show)}
                    >
                      {show ? "Hide" : "Show"}
                    </Button>
                  </InputRightElement>
                </InputGroup>
              </FormControl>
              {newProfilePic ? (
                <FormControl id="pic">
                  <FormLabel>upload picture</FormLabel>
                  <Input
                    type={"file"}
                    p={1.5}
                    accept="image/*"
                    //   onChange={(e) => postData(e.target.files[0])}
                  />
                </FormControl>
              ) : (
                <Text onClick={() => setNewProfilePic(true)}>
                  click if you also want to update your profile picture
                </Text>
              )}

              <Button
                color={"red"}
                width="100%"
                mt={15}
                // onClick={handleSubmit}
                isLoading={loading}
              >
                edit profile
              </Button>
            </VStack>
          </ModalBody>

          <ModalFooter>
            <Button colorScheme="blue" mr={3} onClick={onClose}>
              Close
            </Button>
            {/* <Button variant="ghost">Secondary Action</Button> */}
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
}
